<template>
  <div class="records">
    <div class="list">
      <el-card class="box-card" v-for="(item, index) in list" :key="index">
        <div class="flex-item">{{item.userName}}</div>
        <div class="flex-item">
          <el-timeline>
            <el-timeline-item :color="item.status | colorFilter">{{item.status | statusFilter}}</el-timeline-item>
          </el-timeline>
        </div>
        <div class="flex-item">{{item.phone}}</div>
        <div class="flex-item">{{item.amount}}</div>
        <div class="flex-item">{{item.address}}</div>
      </el-card>
    </div>
    <div class="clearfix" v-show="total > 0">
      <el-pagination
        class="pagenation right"
        background
        @current-change="handleCurrentChange"
        :current-page="query.page"
        :page-size="query.pageSize"
        layout="prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
  </div>
</template>
<script>
import services from "@/services";
export default {
  name: "record",
  data() {
    return {
      list: [],
      total: 0,
      query: {
        page: 1,
        pageSize: 10
      }
    };
  },

  filters: {
    statusFilter(value) {
      //123',
      let label = "";
      switch (value) {
        case 2:
          label = "";
          break;
        case 3:
          label = "";
          break;
        default:
          label = "";
      }

      return label;
    },

    colorFilter(value) {
      let color = "#E6A23C";
      switch (value) {
        case 2:
          color = "#F56C6C";
          break;
        case 3:
          color = "#67C23A";
          break;
        default:
          color = "#E6A23C";
      }

      return color;
    }
  },

  methods: {
    getData() {
      services
        .applyList(this.query)
        .then(res => {
          if (res.data.code !== 0) {
            return Promise.reject(res.data.msg);
          }
          this.total = res.data.data.total;
          this.list = res.data.data.records;
        })
        .catch(err => {
          this.$msgbox({
            type: "error",
            message: err
          });
        });
    },

    handleCurrentChange(val) {
      this.query.page = val;
    }
  }
};
</script>
<style lang="scss" scoped>
.records {
  padding: 24px 32px;
}

.box-card {
  display: flex;
}

.flex-item {
  width: 50%;
  padding: 5px 10px;
  flex: 0 0 auto;
}
</style>