<template>
	<div class="wrap article-width">
		<div class="header">
			<label></label>
			<el-select v-model="type" @change="handleTypeChange">
				<el-option
					v-for="(option) in articleOptions"
					:key="option.value"
					:label="option.label"
					:value="option.value"
				></el-option>
			</el-select>
		</div>

		<div class="table">
			<el-table :data="tableData">
				<el-table-column type="index"></el-table-column>
				<el-table-column prop="title" label="" min-width="200px"></el-table-column>
				<el-table-column prop="authorName" label="" min-width="80px"></el-table-column>
				<el-table-column prop="createTime" label="" min-width="100px"></el-table-column>
				<el-table-column label="" min-width="100px">
					<template slot-scope="scope">
						<el-button @click="handleModify(scope.row)" type="text" size="small"></el-button>
						<el-button @click="handleModify(scope.row)" type="text" size="small"></el-button>
						<el-button
							v-if="scope.row.status === 1"
							@click="handlePublish(scope.row)"
							type="text"
							size="small"
						></el-button>
						<el-button v-else @click="handleCancelPublish(scope.row)" type="text" size="small"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<div class="clearfix" v-show="total > 0">
				<el-pagination
					class="pagenation right"
					background
					@current-change="handleCurrentChange"
					:current-page="query.page"
					:page-size="query.pageSize"
					layout="prev, pager, next, jumper"
					:total="total"
				></el-pagination>
			</div>
		</div>
	</div>
</template>
<script>
	import { mapGetters } from "vuex";
	import services from "@/services";
	export default {
		name: "ArticleManage",

		data() {
			return {
				total: 0,
				type: "bingchong",
				query: {
					page: 1,
					pageSize: 10
				},
				tableData: []
			};
		},

		computed: {
			...mapGetters(["articleOptions"])
		},

		created() {
			this.getData();
		},

		methods: {
			getData() {
				let type = this.type;
				if (type === "bingchong") {
					services
						.getArticleList(this.query)
						.then(res => {
							if (res.data.code !== 0) {
								return Promise.reject(res.code.msg);
							}
							let data = res.data.data;
							this.tableData = data.records;
							this.total = data.total;
						})
						.catch(err => {
							this.$msgbox({ type: "error", message: err });
						});
				} else if (type === "zaipei") {
					services
						.getZaiPeiArticleList(this.query)
						.then(res => {
							if (res.data.code !== 0) {
								return Promise.reject(res.code.msg);
							}
							let data = res.data.data;
							this.tableData = data.records;
							this.total = data.total;
						})
						.catch(err => {
							this.$msgbox({ type: "error", message: err });
						});
				} else if (type === "summary") {
					services
						.getSummaryArticleList(this.query)
						.then(res => {
							if (res.data.code !== 0) {
								return Promise.reject(res.code.msg);
							}
							let data = res.data.data;
							this.tableData = data.records;
							this.total = data.total;
						})
						.catch(err => {
							this.$msgbox({ type: "error", message: err });
						});
				} else if (type === "notice") {
					services
						.getNoticeArticleList(this.query)
						.then(res => {
							if (res.data.code !== 0) {
								return Promise.reject(res.code.msg);
							}
							let data = res.data.data;
							this.tableData = data.records;
							this.total = data.total;
						})
						.catch(err => {
							this.$msgbox({ type: "error", message: err });
						});
				}
			},

			handleTypeChange() {
				this.query.page = 1;
				this.getData();
			},

			handleCurrentChange(val) {
				this.query.page = val;
				this.getData();
			},

			handleDelete(row) {
				services
					.getArticleList(row.id)
					.then()
					.catch(err => {
						console.log(err);
					});
			},

			handleModify(row) {
				services
					.deleteArticle(row.id)
					.then()
					.catch(err => {
						console.log(err);
					});
			},

			handlePublish(row) {
				services
					.getArticleList(row.id)
					.then()
					.catch(err => {
						console.log(err);
					});
			},

			handleCancelPublish(row) {
				services
					.getArticleList(row.id)
					.then()
					.catch(err => {
						console.log(err);
					});
			}
		}
	};
</script>
<style lang="scss" scoped>
	.wrap {
		padding: 24px 30px;
	}

	.header {
		margin-bottom: 24px;
	}

	.pagenation {
		margin-top: 24px;
		padding: 0 20px;
	}
</style>