import axios from 'axios';
// import router from '../route/index';
// import { message } from 'element-ui';
import store from '@/store';
import router from '@/route';
import { Message } from 'element-ui'

axios.defaults.baseURL = 'http://124.156.99.143:8099';

// 
axios.interceptors.request.use(function (config) {
  config.headers.token = store.getters.token
  // 
  return config;
}, function (error) {
  // 
  return Promise.reject(error);
});

// 
axios.interceptors.response.use(function (response) {
  // 
  if (response.data.code === 401) {
    Message.error('')
    store.commit({
      type: 'logout'
    })
    router.push('login')
  }
  return response;
}, function (error) {

  // 
  return Promise.reject(error);
});

const api = {
  file: {
    uploadUrl: axios.defaults.baseURL + '/common/upload',
    baseURL: axios.defaults.baseURL
  },
  register(data) {
    let url = '/common/register'
    return axios.post(url, data)
  },
  login(data) {
    let url = '/common/login'
    return axios.post(url, data)
  },
  getSummaryArticleList(data) {
    let url = '/common/sumup'
    return axios.get(url, {
      params: data
    })
  },
  addSummary(data) {
    let url = '/common/sumup'
    return axios.post(url, data)
  },
  getSearchArticleList(query) {
    let url = "/common/article/search"
    return axios.get(url, {
      params: query
    })
  },
  getZaiPeiArticleList(query) {
    let url = ""
    return axios.get(url, {
      params: query
    })
  },
  getNoticeArticleList(query) {
    let url = "/common/notice"
    return axios.get(url, {
      params: query
    })
  },
  addNotice(data) {
    let url = "/admin/notice"
    return axios.post(url, data)
  },
  getNoticeDetail(id) {
    let url = "/common/notice/" + id
    return axios.get(url)
  },
  getBingchongArticleList(query) {
    let url = ""
    return axios.get(url, {
      params: query
    })
  },

  addApply(data) {
    let url = '/user/apply'
    return axios.post(url, data)
  },

  applyList(data) {
    let url = '/user/apply'
    return axios.get(url, { params: data })
  },

  addArticle(data) {
    let url = '/tech/article'
    return axios.post(url, data)
  },

  getArticleList(data) {
    let url = '/tech/article'
    return axios.get(url, {
      params: data
    })
  },
  getComment(data) {
    let url = '/user/comment/' + data.id
    return axios.get(url, { params: { page: data.page, pageSize: data.pageSize } })
  },

  modifyPassword(data) {
    let url = ""
    return axios.post(url, data)
  }
}

export default api