<template>
  <div class="article-detail article-width">
    <el-page-header @back="goBack" class="page-header" content=""></el-page-header>

    <div class="article-content">
      <div class="field-row title">
        <span>{{articleDetail.title}}</span>
      </div>
      <div class="field-row author clearfix">
        <span class="left">{{articleDetail.authorName}}</span>
        <span class="right">{{articleDetail.createTime}}</span>
      </div>
      <!-- <div class="field-row img" v-if="articleDetail.coverImg">
        <img :src="articleDetail.coverImg" alt />
      </div>-->
      <div class="field-row">
        <div class="content" v-html="articleDetail.content"></div>
      </div>
    </div>

    <div class="comment" v-if="showComent">
      <div class="comment-item" v-for="(comment, index) in commentList" :key="index">
        <div class="comment-header">
          <span>{{comment.userName}}</span>
          <span>{{comment.createTime}}</span>
        </div>
        <div class="comment-content">{{comment.content}}</div>
      </div>
    </div>
  </div>
</template>
<script>
import services from "@/services";
export default {
  name: "ArticleDetail",

  props: {
    type: {
      type: String,
      required: true
    },
    id: {
      type: [String, Number],
      required: true
    }
  },

  data() {
    return {
      articleDetail: {
        authorName: 123,
        // coverImg: null,
        title: 123,
        content: 123,
        createTime: 123
      },
      commentList: [],
      commentQuery: {
        page: 1,
        pageSize: 10
      }
    };
  },

  computed: {
    showComent() {
      let type = this.type;
      if (type === "bingchong" || type === "zaipei" || type === "summary") {
        return true;
      }

      return false;
    }
  },

  created() {
    this.getDetail();
    this.showComent && this.getComment();
  },

  methods: {
    goBack() {
      this.$router.go(-1);
    },

    showError(err) {
      this.$msgbox({
        type: "error",
        message: err
      });
    },

    getDetail() {
      if (this.type === "notice") {
        services
          .getNoticeDetail(this.id)
          .then(res => {
            this.articleDetail = res.data.data;
          })
          .catch(err => {
            this.showError(err);
          });
      } else if (this.type === "bingchong" || this.type === "zaipei") {
        services
          .getNoticeDetail(this.id)
          .then(res => {
            this.articleDetail = res.data.data;
          })
          .catch(err => {
            this.showError(err);
          });
      }
    },

    getComment() {
      services
        .getComment({ id: this.id, ...this.commentQuery })
        .then(res => {
          this.commentList = res.data.data;
          // userName":"",
          //   "content":"222",
          //   "createTime":"2020-04-08T14:31:52",
          //   "updateTime":"2020-04-08T14:35:32",
        })
        .catch(err => {
          this.showError(err);
        });
    }
  }
};
</script>
<style lang="scss" scoped>
.article-detail {
  padding: 24px 32px;
}

.page-header {
  border-bottom: 1px solid #eee;
  padding-bottom: 16px;
}

.article-content {
}

.avatar {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
}

.field-row {
  line-height: 1.5;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.85);
  margin-top: 24px;
}

.title {
  font-size: 18px;
  text-align: center;
}

.author,
.time {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.45);
  padding: 0 20px;
}

.img {
  text-align: center;
}

.content {
  display: inline-block;
  color: rgba(0, 0, 0, 0.75);
}

.comment {
  margin-top: 30px;
}
</style>